// Header
header {
	position: absolute;
	top: 0rem;
	left: 0rem;
	width: 100%;
	nav.menu {
		ul {
			list-style: none;
			display: flex;
			justify-content: space-around;
			a {
				text-decoration: none;
				color: #fff; }
			li {
				padding: 0.2rem 0.5rem;
				border-radius: .3rem {
                list-style: none; }
				&:hover {
					background: #643a79; } } } } }
.bg-new {
    background-color: lighten(black,70%); }

