.home_skills {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    h2,p,h4 {
        color: $mauchu; }
    p {
        font-size: 13pt; }
    h2 {
        .covien {}
        position: relative;
        letter-spacing: .5rem;
        margin-top: 2rem;
        &:after {
            content: "";
            width: 14%;
            height: 4px;
            background: #dfe8ed;
            position: absolute;
            bottom: 0;
            left: 43%; } }

    .progress {
        width: 150px;
        height: 150px;
        line-height: 150px;
        background: none;
        margin: 0 auto;
        box-shadow: none;
        position: relative;
        &:after {
            content: "";
            width: 100%;
            height: 100%;
            border-radius: 50%;
            border: 12px solid #fff;
            position: absolute;
            top: 0;
            left: 0; }
        > span {
            width: 50%;
            height: 100%;
            overflow: hidden;
            position: absolute;
            top: 0;
            z-index: 1; }
        .progress-left {
            left: 0; }
        .progress-bar {
            width: 100%;
            height: 100%;
            background: none;
            border-width: 12px;
            border-style: solid;
            position: absolute;
            top: 0; }
        .progress-left .progress-bar {
            left: 100%;
            border-top-right-radius: 80px;
            border-bottom-right-radius: 80px;
            border-left: 0;
            -webkit-transform-origin: center left;
            transform-origin: center left; }
        .progress-right {
            right: 0;
            .progress-bar {
                left: -100%;
                border-top-left-radius: 80px;
                border-bottom-left-radius: 80px;
                border-right: 0;
                -webkit-transform-origin: center right;
                transform-origin: center right;
                animation: loading-1 1.8s linear forwards; } }
        .progress-value {
            width: 90%;
            height: 90%;
            border-radius: 50%;
            font-size: 24px;
            color: #3c4761;
            line-height: 135px;
            text-align: center;
            position: absolute;
            top: 5%;
            left: 5%; }
        &.blue {
            .progress-bar {
                border-color: #30bae7; }
            .progress-left .progress-bar {
                animation: loading-2 1.5s linear forwards 1.8s; } }
        &.yellow {
            .progress-bar {
                border-color: #d74680; }
            .progress-left .progress-bar {
                animation: loading-3 1s linear forwards 1.8s; } }
        &.pink {
            .progress-bar {
                border-color: #15c7a8; }
            .progress-left .progress-bar {
                animation: loading-4 0.4s linear forwards 1.8s; } }
        &.green {
            .progress-bar {
                border-color: #eb7d4b; }
            .progress-left .progress-bar {
                animation: loading-5 1.2s linear forwards 1.8s; } }

        @keyframes loading-1 {
            0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg); }
            100% {
                -webkit-transform: rotate(180deg);
                transform: rotate(180deg); } }
        @keyframes loading-2 {
            0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg); }
            100% {
                -webkit-transform: rotate(144deg);
                transform: rotate(144deg); } }
        @keyframes loading-3 {
            0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg); }
            100% {
                -webkit-transform: rotate(90deg);
                transform: rotate(90deg); } }
        @keyframes loading-4 {
            0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg); }
            100% {
                -webkit-transform: rotate(36deg);
                transform: rotate(65deg); } }
        @keyframes loading-5 {
            0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg); }
            100% {
                -webkit-transform: rotate(126deg);
                transform: rotate(126deg); } }
        @media only screen and (max-width: 990px) {
            .progress {
                margin-bottom: 20px; } } } }
